import { Component, OnInit, ANALYZE_FOR_ENTRY_COMPONENTS } from '@angular/core';
import {AuthenticationService} from '../services/loginservice';
import {HttpRequestService} from '../services/httpsevice.service';

@Component({
  selector: 'app-display-super',
  templateUrl: './display-super.component.html',
  styleUrls: ['./display-super.component.css']
})
export class DisplaySuperComponent implements OnInit {
  
  public objectKeys=Object.keys;
  public editAdmin:boolean=false;
  private usernameList:string[]=undefined;
  public suscription:boolean;
  public adminList:any;
  public selectedAdmin:any;
  public usernameStatus:boolean=true;
  public username:string;
  public add:boolean=false;
  constructor(private httpHandle:HttpRequestService,private credentials:AuthenticationService){
    this.adminList= [
      {
        uid : 6,
        firstname:'Abhiyan',
        lastname :'Timilsina',
        username :'abhiyan',
        role : 'admin'
      }
    ];
    this.selectedAdmin=0;
   }

   displayAdmin(event:any)
   {
     this.selectedAdmin=event.target.getAttribute('id');
   }
  ngOnInit() {
    var url = `users/2/${this.credentials.getToken.id}`;
    this.suscription=true;
    this.httpHandle.getData1(url,this.credentials.getToken);
    this.httpHandle.callComplete1.subscribe((data)=>{
      this.adminList = data.result;
     
      });
  }
  addUser(event)
  {
    this.add=!this.add;
  }
  addNewUser(formValue)
  {
    
    this.add=!this.add;
   formValue.sid=this.credentials.getToken.id;
   formValue.role="admin";
    this.httpHandle.postRequestWithCredentials("users",{
      id : this.credentials.getToken.id,
      token: this.credentials.getToken.token
    },formValue).subscribe(()=>{
      var url = `users/2/${this.credentials.getToken.id}`;
      this.httpHandle.getData1(url,this.credentials.getToken);
    this.httpHandle.callComplete1.subscribe((data)=>{
    
      this.adminList = data.result;
      });
    });
  }

  destroyAdmin(event)
  { 
     this.adminList.forEach(element => {
        if(event.id==element.UID)
        {
       
          this.adminList.splice(this.adminList.indexOf(element),1);
        }
     });
  }
  haze(event)
  {

    this.usernameStatus=true;
    if(this.usernameList==undefined)
    this.httpHandle.getUserList('users/0/1',{
      id: this.credentials.getToken.id,
      token : this.credentials.getToken.token
    }).subscribe((data:any)=>{
        
         this.usernameList=data.result.map(element=>element.username);
          if(this.usernameList.indexOf(this.username)!=-1)
            this.usernameStatus=false;
      });
    else
     {
      if(this.usernameList.indexOf(this.username)!=-1)
      this.usernameStatus=false;
     }
  }

  public perform(event)
  {
    this.editAdmin=false;
    this.httpHandle.postRequestWithCredentials("modifyUser",{
      id : this.credentials.getToken.id,
      token: this.credentials.getToken.token
    },event).subscribe(()=>{
      var url = `users/2/${this.credentials.getToken.id}`;
      this.httpHandle.getData1(url,this.credentials.getToken);
    this.httpHandle.callComplete1.subscribe((data)=>{
    
      this.adminList = data.result;
      });
    });
  }

}
