import { Injectable, EventEmitter } from "@angular/core";
import { HttpRequestService } from "./httpsevice.service";



@Injectable()
export class AuthenticationService
{
    authenticatedEvent= new EventEmitter<any>();
     authenticated:boolean=false;
     userInfo:any={};
     
    constructor(private httpHandle:HttpRequestService){
    }

    get getToken():any{
        return {token : window.sessionStorage.getItem('token'),
            id:window.sessionStorage.getItem('id')};
    }

    authorizeUser(username:string,password:string){
      this.httpHandle
      .postRequest('authorize',{username:username,password:password}).subscribe((data:any)=>{
        
        if(data.statusCode==200)
          { 
             
              this.setToken(data.token,data.id);
              this.authenticated=true;
              this.userInfo=data.userInfo;
              this.authenticatedEvent.emit(this.userInfo);
          }
          else
          {
              this.authenticatedEvent.emit("ACCESS DENIED");
          }
      });
     }
     setToken(token,id)
     {
         this.authenticated=true;
         window.sessionStorage.clear(); 
         window.sessionStorage.setItem('token',token);
         window.sessionStorage.setItem('id',id);
     }

     destroySession()
     {
         this.authenticated=false;
         window.sessionStorage.clear();
         this.userInfo=undefined;
     }
     
}