import {HttpClient} from '@angular/common/http';
import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class HttpRequestService {
 
    private completeUrl:string;
    private baseUrl:string="/";
    callComplete = new EventEmitter<any>();
    callComplete1 = new EventEmitter<any>();
    constructor(private httpclient:HttpClient)
    {
    }

    public getData1(url,credentials)
    {
     
       this.completeUrl=this.baseUrl+url+`?id=${credentials.id}&token=${credentials.token}`;
       this.httpclient.get(this.completeUrl).subscribe((data:any)=>{
      
          if(data.statusCode != 200 )
          {
            this.callComplete1.emit({
              responseId:1,
              type:'faliure',
              code:data.statusCode,
              result : []
            });
          }
          else
          {
            this.callComplete1.emit({
              responseId:1,
              type:'success',
              code:data.statusCode,
              result : data.result
            });
          }
       });

    }

    public getData(url,credentials)
    {
     
     
       this.completeUrl=this.baseUrl+url+`?id=${credentials.id}&token=${credentials.token}`;
 
       this.httpclient.get(this.completeUrl).subscribe((data:any)=>{

      
          if(data.statusCode != 200 )
          {
            this.callComplete.emit({
              responseId:1,
              type:'faliure',
              code:data.statusCode,
              result : []
            });
          }
          else
          {
            this.callComplete.emit({
              responseId:1,
              type:'success',
              code:data.statusCode,
              result : data.result
            });
          }
       });

    }
   public postRequest(url,body)
   {
      url="/"+url;
     return this.httpclient.post(url,body)
   }
   public postRequestWithCredentials(url,credentials,body)
   {
     
    this.completeUrl=`/modifyUser?id=${credentials.id}&token=${credentials.token}`;
    if(url!=undefined)
    this.completeUrl=`/${url}?id=${credentials.id}&token=${credentials.token}`;
    return this.httpclient.post(this.completeUrl,body);
   }
   getUserList(url,credentials)
   {
    
      this.completeUrl="/"+url+`?id=${credentials.id}&token=${credentials.token}`;
       return this.httpclient.get(this.completeUrl);  
    
  }
 }

