import { Component, OnInit,Input, ViewChild, ElementRef, Output, EventEmitter} from '@angular/core';
import {HttpRequestService} from "../services/httpsevice.service";
import {AuthenticationService} from '../services/loginservice';

@Component({
  selector: 'app-useredit',
  templateUrl: './useredit.component.html',
  styleUrls: ['./useredit.component.css']
})
export class UsereditComponent implements OnInit {
   @Input('toEdit') user:any;
   @ViewChild('editbox') editbox:ElementRef;
   @Output() dbEvent = new EventEmitter<any>();
   public singleEdit:boolean;
   public field:string;
   private usernameList:string[]=undefined;
   public usernameStatus:boolean=true;
   public firstname:string;
   public lastname:string;
   public username:string;
   public password:string;


  public value:string;
 
  constructor(private httpHandle:HttpRequestService,private credentials:AuthenticationService) {
    this.singleEdit=false;
  }

  ngOnInit() {
  }
 updateSingle(event)
 {
   
   this.singleEdit=true;
   this.field= event.target.parentElement.getAttribute('type');
 }


haze(formValue)
{
 if(this.singleEdit)
 {
 
  var obj ={};
  obj[this.field]=formValue.value;
  obj["type"]=1;
  obj["id"]=this.user.UID;
  obj["update"]=true;
  this.dbEvent.emit(obj);
 }
 else
 {
this.dbEvent.emit({
     update: true,
     type : 1,
     id:this.user.UID,
     firstname: this.firstname,
     lastname: this.lastname,
     username: this.username,
     password: this.password,
     })
 }
}
haze1(event)
{

  this.usernameStatus=true;
  if(this.usernameList==undefined)
  this.httpHandle.getUserList('users/0/1',{
    id: this.credentials.getToken.id,
    token : this.credentials.getToken.token
  }).subscribe((data:any)=>{
      
       this.usernameList=data.result.map(element=>element.username);
        if(this.usernameList.indexOf(this.username)!=-1)
          this.usernameStatus=false;
    });
  else
   {
    if(this.usernameList.indexOf(this.username)!=-1)
    this.usernameStatus=false;
   }
}
}