import { Component} from '@angular/core';
import { HttpRequestService } from './services/httpsevice.service';
import {Router} from '@angular/router';
import {AuthenticationService} from './services/loginservice';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent{
  authorized:boolean=false;
  type:string;
  constructor(private router:Router ,private httpservice:HttpRequestService,private authorize:AuthenticationService){
    this.authorize.authenticatedEvent.subscribe((data)=>{
      this.type=data.role;
      this.authorized=true;
    }); 
  }
  
  ngOnInit()
  {
    this.authorized = this.authorize.getToken.token==undefined?false:true;
  }
  logout()
  {
    this.authorized=true;
    this.type=undefined;
    this.authorize.destroySession();
    this.router.navigate(['']);

  }
}
