import { Component, OnInit,Output, Input, EventEmitter } from '@angular/core';
import {AuthenticationService} from '../services/loginservice';
import {HttpRequestService} from '../services/httpsevice.service';

@Component({
  selector: 'app-display',
  templateUrl: './display.component.html',
  styleUrls: ['./display.component.css']
})
export class DisplayComponent implements OnInit {
  private usernameList:string[]=undefined;
  public usernameStatus:boolean=true; 
  public userList:any[];
   @Output('adminDeleteEvent') adminDel=new EventEmitter<{id:number}>();
   @Input('id') adminId:number;
   public dataLoaded:boolean=false;
   public updateFlag:boolean=false;
   public toUpdate:any;
   public add:boolean=false;
   public username:string;
  constructor(private credentials:AuthenticationService,private httpHandle:HttpRequestService){
    this.userList=[
  ]
  }
  ngOnInit() {
   
    var url;
    if(this.adminId==undefined)
    this.adminId=this.credentials.userInfo.UID;
    url = `users/1/${this.adminId}`;
    this.httpHandle.getData(url,{id: this.credentials.getToken.id,
      token: this.credentials.getToken.token
   });
  
   this.httpHandle.callComplete.subscribe((data)=>{
    
    this.userList = data.result;
     this.dataLoaded=true;
   });

    
  }
  update(event:any)
  {
     var id =event.target.getAttribute('id');
   
     this.toUpdate=this.userList[id];
 
     this.updateFlag=true;
  }
  perform(event:any)
  {
    
    if(event.update)
     this.updateFlag=false;

    var url="modifyUser";
    this.httpHandle.postRequestWithCredentials(url,{
      id:this.credentials.getToken.id,
      token:this.credentials.getToken.token}
    ,event).subscribe((data)=>{
   
      var url = `users/1/${this.adminId}`;
      this.httpHandle.getData(url,{id: this.credentials.getToken.id,
        token: this.credentials.getToken.token
     });
    
     this.httpHandle.callComplete.subscribe((data)=>{
       
      this.userList = data.result;
       this.dataLoaded=true;
     });
    });
   
  
  }
  delete(event:any)
  {
    var id =parseInt(event.target.getAttribute('id'));
    this.perform({type:0,
      id : id
    });
    if(event.target.hasAttribute('t'))
    this.adminDel.emit({id:this.adminId});
 }
 addUser(event)
 {
   this.add=!this.add;
 }
 addNewUser(formValue)
 {
 

   this.add=!this.add;
 
   formValue.aid=this.adminId;
   formValue.role="user"; 
   this.httpHandle.postRequestWithCredentials("users",{
     id : this.credentials.getToken.id,
     token: this.credentials.getToken.token
   },formValue).subscribe((data)=>{
   
var url = `users/1/${this.adminId}`;
    this.httpHandle.getData(url,{id: this.credentials.getToken.id,
      token: this.credentials.getToken.token
   });
  
   this.httpHandle.callComplete.subscribe((data)=>{
     
    this.userList = data.result;
     this.dataLoaded=true;
   });
});
 }
 haze(event)
 {

   this.usernameStatus=true;
   if(this.usernameList==undefined)
   this.httpHandle.getUserList('users/0/1',{
     id: this.credentials.getToken.id,
     token : this.credentials.getToken.token
   }).subscribe((data:any)=>{
       
        this.usernameList=data.result.map(element=>element.username);
         if(this.usernameList.indexOf(this.username)!=-1)
           this.usernameStatus=false;
     });
   else
    {
     if(this.usernameList.indexOf(this.username)!=-1)
     this.usernameStatus=false;
    }
 }

}
